import { GLShaderCommon } from '../../../Utility/GLShaderCommon';

import { MeshBase } from '../Core/Base';
import { NGL3dApp } from '../../..';

import { Transform } from '../../../WebGL/Transform';

export class Rect extends MeshBase {

    v = `#version 300 es

    in vec3 a_position;
    in vec2 a_uv;

    uniform mat4 uPMatrix;
    uniform mat4 uMVMatrix;
    uniform mat4 uCameraMatrix;

    uniform vec2 u_resolution;

    out vec2 uv;

    void main() {
        // gl_Position = vec4(a_position , 0, 1);
        gl_Position = uPMatrix * uCameraMatrix * uMVMatrix * vec4( a_position.x, a_position.y, a_position.z, 1.0);
        uv = a_uv;
    }
    `;

    f = `#version 300 es

    precision mediump float;
    uniform vec4 u_color;
    in vec2 uv;

    out vec4 outColor;

    void main() {

			float c = (uv.x <= 0.1 || uv.x >=0.9 || uv.y <= 0.1 || uv.y >= 0.9)? 0.0 : 1.0;
			outColor = vec4(c,c,c,1.0-c);
    }
    `;

    app: NGL3dApp;

    gl: WebGL2RenderingContext;
    program: WebGLProgram | null;

    positionAttributeLocation: number;

    perspective: WebGLUniformLocation | null;
    modelMatrix: WebGLUniformLocation | null;
    cameraMatrix: WebGLUniformLocation | null;
    colorLocation: WebGLUniformLocation | null;

    positionBuffer: WebGLBuffer | null;
    vao: WebGLVertexArrayObject | null;

    translation: number[] = [];
    color: number[] = [];
    width: number;
    height: number;

    mesh: Float32Array;

    transform: Transform;

    constructor(app: NGL3dApp) {
        super(app.scene);
        this.app = app;
        this.gl = app.canvas.gl;

        this.program = GLShaderCommon.getShaderProgram(this.gl, this.v, this.f);

        if (this.program) {
            this.positionAttributeLocation = this.gl.getAttribLocation(this.program, 'a_position');

            this.perspective = this.gl.getUniformLocation(this.program, 'uPMatrix');
            this.modelMatrix = this.gl.getUniformLocation(this.program, 'uMVMatrix');
            this.cameraMatrix = this.gl.getUniformLocation(this.program, 'uCameraMatrix');

            this.colorLocation = this.gl.getUniformLocation(this.program, 'u_color');
        }
        this.positionBuffer = this.gl.createBuffer();
        this.vao = this.gl.createVertexArray();
        this.gl.bindVertexArray(this.vao);

        this.gl.bindBuffer(this.gl.ARRAY_BUFFER, this.positionBuffer);
        this.gl.enableVertexAttribArray(this.positionAttributeLocation);

        // Tell the attribute how to get data out of positionBuffer (ARRAY_BUFFER)
        var size = 3;          // 2 components per iteration
        var type = this.gl.FLOAT;   // the data is 32bit floats
        var normalize = false; // don't normalize the data
        var stride = 0;        // 0 = move forward size * sizeof(type) each iteration to get the next position
        var offset = 0;        // start at the beginning of the buffer
        this.gl.vertexAttribPointer(this.positionAttributeLocation, size, type, normalize, stride, offset);

        // First let's make some variables
        // to hold the translation, width and height of the rectangle
        this.translation = [-1, -1];
        this.color = [Math.random(), Math.random(), Math.random(), 1];
        this.width = 2;
        this.height = 2;

        this.mesh = new Float32Array(this.getRectangle(this.translation[0], this.translation[1], this.width, this.height));

        this.transform = new Transform();
        this.gl.useProgram(this.program);

        // this.gl.uniform2f(this.resolutionUniformLocation, this.gl.canvas.width, this.gl.canvas.height);
        this.gl.uniform4fv(this.colorLocation, this.color);

        this.gl.uniformMatrix4fv(this.perspective, false, app.camera.projectionMatrix);
        this.gl.uniformMatrix4fv(this.modelMatrix, false, this.transform.getViewMatrix());
        this.gl.uniformMatrix4fv(this.cameraMatrix, false, app.camera.viewMatrix);
        this.gl.useProgram(null);

    }
    getRectangle(x: number, y: number, width: number, height: number) {
        let x1 = x;
        let x2 = x + width;
        let y1 = y;
        let y2 = y + height;

        return [
            x1, y1, 0,
            x2, y1, 0,
            x1, y2, 0,
            x1, y2, 0,
            x2, y1, 0,
            x2, y2, 0,
        ];
    }
    setPerspective(matData: Float32Array) {
        this.gl.uniformMatrix4fv(this.perspective, false, matData);
        return this;
    }
    setModelMatrix(matData: Float32Array) {
        this.gl.uniformMatrix4fv(this.modelMatrix, false, matData);
        return this;
    }
    setCameraMatrix(matData: Float32Array) {
        this.gl.uniformMatrix4fv(this.cameraMatrix, false, matData);
        return this;
    }
    render() {
        this.update();
        this.gl.useProgram(this.program);
        this.transform.updateMatrix();
        this.setModelMatrix(this.transform.getViewMatrix());
        this.setPerspective(this.app.camera.projectionMatrix);
        this.setCameraMatrix(this.app.camera.viewMatrix);

        this.gl.bindVertexArray(this.vao);
        this.gl.bindBuffer(this.gl.ARRAY_BUFFER, this.positionBuffer);
        this.gl.bufferData(this.gl.ARRAY_BUFFER, this.mesh, this.gl.STATIC_DRAW);

        // Draw the rectangle.
        var primitiveType = this.gl.TRIANGLES;
        var offset = 0;
        var count = 6;
        this.gl.drawArrays(primitiveType, offset, count);

    }
    update() {
        this.transform.position.x += 0.01;
    }

}